<template>
  <div>
    <div v-for="item in nameList" :key="item.id" class='columns block m-2'>
      <div class="box column is-half is-offset-one-quarter">
        {{ item.ITEM_NAME }}
        <span v-if="item.type == 'nutrition'" class="m-1">
          <button class="is-small button is-link is-rounded is-light"
            v-on:click="activate(item.id)">Info</button>
        </span>
        <Badge :type="item.type"/>
      </div>
      <InfoModal v-if="item.type == 'nutrition'" 
                 :activated="activatedInfo===item.id ? true : false" 
                 @hide-modal="hideModal"
                 :type="item.type"
                 :info="item.data"/>
    </div>
  </div>
</template>

<script>
import Badge from './Badge.vue'
import InfoModal from './InfoModal.vue'

export default {
  name: "ResultList",
  props: ['resultList'],
  components: {
    Badge,
    InfoModal,
  },
  data: function(){
    return {
      activatedInfo: '',
    }
  },
  mounted: function(){
  },
  updated: function(){
  },
  methods: {
    checkActivation: function(id){
      if(this.activatedInfo.includes(id))
        return true
      return false
    },
    activate: function(id){
      this.activatedInfo = id
    },
    hideModal: function(){
      this.activatedInfo = ''
    }
  },
  computed: {
    nameList: function(){
      let list = []
      for(let id in this.resultList){
        list.push(this.resultList[id])
      }
      return list
    }
  },
}
</script>

