<template>
  <div :class="modalClass">
    <div class="modal-background"></div>
    <div class="modal-card">
      <header class="modal-card-head">
        <p class="modal-card-title" v-if="info">{{ info.ITEM_NAME }}</p>
        <button class="delete" aria-label="close" @click="$emit('hide-modal')"></button>
      </header>
      <section class="modal-card-body">
        <div v-if="type == 'nutrition'">
          <table class="table is-bordered is-striped is-narrow is-hoverable" style="width:100%">
            <colgroup>
              <col span="1" style="width: 20%;">
              <col span="1" style="width: 35%;">
              <col span="1" style="width: 20%;">
            </colgroup>
            <tbody>
              <tr v-for="(val, key) in processedInfo" :key="key" class="">
                <td :class="colorClass(val, key)"><strong>{{ formatTitle(key) }}</strong></td>
                <td>{{ val[0] }}</td>
                <td>{{ val[1] }}</td>
              </tr>
            </tbody>
          </table>
        </div>
      </section>
      <footer class="modal-card-foot">
        <button class="button" @click="$emit('hide-modal')">Close</button>
      </footer>
    </div>
  </div>
</template>

<script>
export default {
  name: "InfoModal",
  props: ['type', 'info', 'activated', 'hideModal'],
  data: function(){
    return {
      isActivate: false
    }
  },
  mounted: function(){
    this.isActivate = this.activated || false
  },
  computed: {
    modalClass: function(){
      return {
        modal: true,
        'is-active': this.activated,
      }
    },
    processedInfo: function(){
      let info = {}
      info.name = [ this.info.ITEM_NAME ]
      info.type = [ this.info.TYPE ]
      info.subtype = [ this.info.SUBTYPE ]
      info.form = [ this.info.FORM ]
      info.protein = [this.info.PROT, "gm/1000kCal"]
      info.fat = [this.info.FAT, "gm/1000kCal"]
      info.carb = [this.info.CARB, "gm/1000kCal"]
      info.Na = [this.info.Na, "mg/1000kCal"]
      info.K = [this.info.K, "mg/1000kCal"]
      info.Cl = [this.info.Cl, "mg/1000kCal"]
      info.Ca = [this.info.Ca, "mg/1000kCal"]
      info.P = [this.info.P, "mg/1000kCal"]
      info.Mg = [this.info.Mg, "mg/1000kCal"]
      if(this.info.FLAVOUR != "")info.flavour = [ this.info.FLAVOUR ]
      info.conc = [ this.info.CONC, "kCal/mL"]
      info.osmolarity = [ this.info.OSM, "mOsm/kg"]
      info.price = [this.info.Price, "Baht"]

      return info
    }
  },
  methods: {
    formatTitle: function(msg){
      msg = msg.charAt(0).toUpperCase() + msg.slice(1);
      return msg
    },
    colorClass: function(val, key){
      key
      let color = {}
      if(val.length == 2 && val[1].includes('gm')){
        color['has-background-success'] = true
        color['has-text-white'] = true
      }
      if(val.length == 2 && val[1].includes('mg')){
        color['has-background-warning'] = true
        color['has-text-white'] = true
      }
      if(key == 'price'){
        color['has-background-danger'] = true
        color['has-text-white'] = true
      }
      return color
    }
  }
}

</script>

