<template>
  <div id="app" class="container">
    <!-- <img alt="Vue logo" src="./assets/logo.png"> -->
    <HelloWorld :msg="searchKeyword"/>
    <SearchBox v-on:search="onSearch"/>
    <section v-if="loading" class="columns">
      <div class="box column is-half is-offset-one-quarter">
        <strong>Loading...</strong>
      </div>
    </section>
    <section v-if="loading == false">
      <ResultList :resultList="resultList"/>
    </section>
  </div>
</template>

<script>
import HelloWorld from './components/HelloWorld.vue'
import SearchBox from './components/SearchBox.vue'
import ResultList from './components/ResultList.vue'

import axios from 'axios'

export default {
  name: 'App',
  components: {
    HelloWorld,
    SearchBox,
    ResultList,
  },
  data: function(){
    return {
      searchKeyword: '',
      resultList: {},
      loading: null,
      errored: false,
    }
  },
  methods: {
    fetchData: async function(){
      let url = 'https://us-central1-med-red-panda.cloudfunctions.net/fetch_search_medrp'
      // If in DEV mode
      if(window.webpackHotUpdate)
        url = "http://localhost:8080"
      // axios
      //   .get(url, {
      //     params: {
      //       keyword: this.searchKeyword
      //     }
      //   })
      //   .then(response => {
      //     this.resultList = response.data
      //     this.loading = false
      //   })
      //   .catch(error => {
      //     console.log(error)
      //     this.errored = true
      //   })
      axios // Not using axios
      const urlParams = new URLSearchParams(window.location.search);

      let getParams = {
        keyword: this.searchKeyword
      }
      if(urlParams.has('med')) getParams.med = true

      let res = await fetch(url+'?'+new URLSearchParams(getParams), {
        method: 'GET',
        mode: 'cors',
        headers: {
          'Content-Type': 'application/json'
        },
      })
      if(res && res.ok){
        this.resultList = await res.json();
        this.loading = false
      }
      else {
        console.log("HTTP-Error: " + res.status);
        this.errored = true
      }
    },
    onSearch: function(value){
      this.loading = true
      this.searchKeyword = value
      this.fetchData(value)
    },
  }
}
</script>

